.ghub-query-load-more {
	&[data-totalpages="1"],
	&[data-totalpages="0"] {
		display: none;
	}

	display: flex;
	.ghub_query_load_more_link {
		cursor: pointer;
	}

	&.justify-content-left {
		justify-content: flex-start;
	}

	&.justify-content-center {
		justify-content: center;
	}

	&.justify-content-right {
		justify-content: flex-end;
	}
}
